<template>
  <div class="goods-manage">
    <div class="box">
      <cube-scroll class="wrapper menu">
        <div class="content">
          <div class="item"><span>面范德萨发生包1</span></div>
          <div class="item cur"><span>面范德萨发生包2</span></div>
          <div class="item"><span>面范德萨发生包3</span></div>
          <div class="item"><span>面范德萨发生包4</span></div>
        </div>
      </cube-scroll>
      <cube-scroll :data="data" class="wrapper list">
        <div class="content">
          <div v-for="item in data" :key="item" class="item cb">
            <img src="http://zhuqiqi.vip/wp-content/uploads/2018/09/img.jpg" alt="">
            <div class="info">
              <h3 class="name">芒果慕斯蛋糕</h3>
              <p class="stock">库存：9999</p>
              <p class="price">￥120.0起</p>
              <div class="fun-box">
                <span @click="edit(item)">编辑</span>
                <span>下架</span>
              </div>
            </div>
          </div>
        </div>
      </cube-scroll>
    </div>
    <my-menu />
  </div>
</template>

<script>
import myMenu from '@/components/menu'

export default {
  components: {
    myMenu
  },
  data () {
    return {
      data: [1, 2, 3, 4, 5, 6]
    }
  },
  methods: {
    edit (item) {
      this.$router.push({ path: '/goodsEdit' })
    }
  }
}
</script>

<style lang="stylus" scoped>
.goods-manage {
  .box {
    display: flex;
    .wrapper {
      position: fixed;
      top: 0;
      bottom: 40px;
      height: inherit;
    }
    .menu {
      left: 0;
      width: 23%;
      background-color: #efefef;
      .item {
        width: 100%;
        display: table;
        height: 50px;
        span {
          display: table-cell;
          padding: 0 8px;
          font-size: 13px;
          line-height: 16px;
          vertical-align: middle;
          text-align: center;
        }
      }
      .cur {
        background-color: #fff;
      }
    }
    .list {
      right: 0;
      width: 77%;
      .item {
        padding: 15px;
        img {
          width: 30%;
          float: left;
        }
        .info {
          width: 68%;
          float: right;
          h3 {
            padding: 5px 0;
            font-size: 16px;
          }
          p {
            line-height: 20px;
            font-size: 14px;
          }
          .fun-box {
            display: flex;
            padding-top: 6px;
            span {
              display: block;
              padding: 5px 15px;
              font-size: 13px;
              border: 1px solid #c30e2f;
              border-radius: 5px;
              color: #c30e2f;
              margin-right: 20px;
            }
          }
        }
      }
    }
  }
}
</style>



